import React from "react";

import "../styles.css";
const PlayList = (props) => {
  const { songs } = props;
  console.log("playlist1", songs[0].title);
  return (
    <div>
      {songs.map((item, index) => {
        return (
          <div className="playlist_container" key={index}>
            <div>
              <button onClick={() => props.handleSongClick(item.id)}>
                play
              </button>
            </div>
            <div>
              <h3>{item.title}</h3>
              <h4>{item.artist}</h4>
            </div>
          </div>
        );
      })}
    </div>
  );
};

export default PlayList;
